body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.contactInfo {
  height: fit-content;
  background-color: #d80404;
  padding: 0.5vw;
  margin: 0;
  text-align: center;
  font-size: 2vw;
  color: white;
  vertical-align: center;
}
.phoneIcon{
  height: 1.5em;
  padding: 0;
  margin: 0;
  vertical-align: middle;
}
.emailIcon{
  height: 1.5em;
  padding: 0;
  margin: 0;
  vertical-align: middle;
}

.navTitle{
  margin: 0;
  padding: 1vw;
  background-color: black;
  color: white;
  font-size: 3vw;
}

.MainLogo{
  margin: 0;
  padding: 0;
  height: 1.5em;
  vertical-align: middle;

}

.homeimg{
  width: 100%;
  height: auto;
  display: block;
  float: left;
}

.homepage{
  position: relative;
  align-items: center;
  border-bottom: 2px;
  overflow: auto;
}

.homebox {
  border-radius: 3px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1vw;
  background-color: rgba(216, 4, 4, 0.78);
  height: fit-content;
  width: fit-content;
  text-align: center;
}

.homeboxtext1{
  
  text-align: center;
  word-wrap: normal;
  font-size: 4vw;
  color: white;
  opacity: 1;
  padding: 0;
  margin: 0;
}

.homeboxtext2{
  
  text-align: center;
  word-wrap: normal;
  font-size: 2vw;
  color: white;
  opacity: 1;
  padding: 0;
  margin: 0;
}

.browseOptions{
  display:inline-block;
  margin: 30px;
  margin-bottom: 0;
  padding:15px;
  border:0.1em solid #000000;
  border-radius:0.12em;
  box-sizing: border-box;
  text-decoration:none;
  font-family:'Roboto',sans-serif;
  background-color: #000000;
  color: white;
  font-size: 1.75vw;
  font-weight: 300;
  text-align:center;
  transition: all 0.6s;
}

.browseOptions:hover{
  color:black;
  background-color:#FFFFFF;
  }

/* about */

.aboutpagetitle{
  color: white;
  background-color: #000000;
  margin: 0;
  padding: 0.8vw;
  padding-left: 1vw;
}